/**/
.col-xs-5ths,
.col-sm-5ths,
.col-md-5ths,
.col-lg-5ths {
    position: relative;
    min-height: 1px;
    padding-right: 10px;
    padding-left: 10px;
}

.col-xs-5ths {
    width: 20%;
    float: left;
}

@media (min-width: 768px) {
    .col-sm-5ths {
        width: 20%;
        float: left;
    }
}

@media (min-width: 992px) {
    .col-md-5ths {
        width: 20%;
        float: left;
    }
}

@media (min-width: 1200px) {
    .col-lg-5ths {
        width: 20%;
        float: left;
    }
}
/**/
html{
  font-size: 14px;
}

@media (max-width: 400px) {
  html{
    font-size:8px;
  }
  #header{
    .navbar-brand{
      padding: 15px 0;
    }
    .navbar-brand img{
      width: 50%;
      height: auto;
    }
  }
  #main{
    .title {
      h1{
        font-size: 5em !important;
      }
    }
  }

}

@media (max-width: 600px) {
  html{
    font-size:10px;
  }

}


body{
  font-family: 'Roboto', sans-serif;
  color: #444444;
  font-size: 1em;

}

#header{
  position: fixed;
  top: 0;
  opacity: 1;
  transition: 0.2s;
  width: 100%;
  background: #fff;
  text-transform: uppercase;
  /*min-height: 75px;*/
  padding-bottom: 15px;
  line-height: 75px;
  border: none;
  font-size: 1.21em;
  .navbar-collapse, .navbar-form {
    border: none;
    margin-top: 20px;
  }
}
#header.scroll{
  opacity: 0;

}
#main{
  position: relative;
  background: #ad2473 url(../img/bg.png) no-repeat;
  background-size: cover;
  height: 1116px;
  /*height: 100vh;*/
  border-bottom: 20px solid #ad2473;
  .title{
    z-index: 10;
    position: absolute;
    top:90px;
    left: 0;
    width: 100%;
    text-align: center;
    color: #fff;
    h1{
      font-weight: bold;
      font-size: 7.85em;
      text-shadow: 0px 0px 20px rgba(0, 0, 0, 0.6);
      margin: 0;
    }
    p{
      font-weight: 300;
      font-size: 3.42em;
      text-shadow: 0px 0px 10px rgba(0, 0, 0, 0.6);
      margin: 20px 0 40px 0;
    }
    .order-button{
      font-size: 2.14em;
      font-weight: bold;
      display: inline-block;
      background: #ffffff77;
      border: 1px solid #fff;
      border-radius: 60px;
      padding: 10px 50px;
      &:any-link{
        text-decoration: none;
        color: #fff;
        transition: 0.2s;
      }
      &:hover{
        background: #ffffff99;
      }
    }
  }
}
#carousel{
  z-index: 1;
  position: absolute;
  bottom: 0;
}

section{
  overflow: hidden;
  position: relative;
  display: block;
  text-align: center;
  padding: 105px 0 85px 0;
  h2{
    text-align: center;
    font-size: 5.35em;
    font-weight: 500;
    color: #444444;
    margin: 0;
  }
}
#level1{
  background: #f7f7f7cc;
  min-height: 1135px;
  overflow: hidden;
  z-index: 100;
  h2{
    margin-bottom: 135px;
    position: relative;
    z-index: 9999;
    color: #44444400;
    transition: 0.6s;
  }
  h3{
    white-space: nowrap;
    font-weight: 500;
    font-size: 1.57em;
  }
  .thumbnail{
    opacity: 0;
    transition: 0.6s;
  }
  &.scr{
    h2{
      color: #444444ff;
      transform: translate3d(0px,20px,0px);
    }
    .thumbnail{
      opacity: 1;
    }
  }

}

#pl1{
  position: absolute;
  transition: 0s;
  top:0;
  left:0;
  width: 100%;
  height: 100%;
  background: #f7f7f7 url(../img/parallax/1.jpg) no-repeat center center;
  opacity: 0.2;
  background-size: cover;
}

#pl2{
  position: absolute;
  top:0;
  left:0;
  width: 100%;
  height: 100%;
  background: #f7f7f7 url(../img/parallax/2.jpg) no-repeat center center;
  opacity: 0.2;
  background-size: cover;
}

#pl3{
  position: absolute;
  top:0;
  left:0;
  width: 100%;
  height: 100%;
  background: #f7f7f7 url(../img/parallax/3.jpg) no-repeat center center;
  opacity: 0.2;
  background-size: cover;
}

#level2{
  h2{
    margin-bottom: 85px;
    color: #44444400;
    transition: 0.6s;
  }
  h3{
    font-weight: 500;
    font-size: 1.42em;
  }
  .thumbnail{
    opacity: 0;
    transition: 0.8s;
  }
  &.scr{
    h2{
      color: #444444ff;
      transform: translate3d(0px,20px,0px);
    }
    .thumbnail{
      opacity: 1;
      transform: translate3d(0px,-20px,0px);
    }
  }
}

#level3{
  background: #f7f7f7cc;
 min-height: 560px;
  h2{
    margin-bottom: 85px;
    color: #44444400;
    transition: 0.6s;
  }
  p{
    display: block;
    margin: 0 0 60px 0;
    font-size: 1.42em;
    opacity: 0;
    transition: 0.6s;
  }
  .thumbnail{
    background: transparent;
    opacity: 0;
    transition: 0.8s;
  }

  &.scr{
    h2{
      color: #444444ff;
      transform: translate3d(0px,20px,0px);
    }
    .thumbnail{
      opacity: 1;
    }
    p{
      opacity: 1;
      transform: translate3d(0px,-20px,0px);
    }
  }

}

#level4{
  background: #ad2473;
  color: #fff;
  h2{
    color: #ffffff00;
    margin-bottom: 80px;
    transition: 0.6s;
  }
  .left-block{
    position: relative;
    left: -20px;
    opacity: 0;
    transition: 1s;
  }
  .right-block{
    position: relative;
    right: -20px;
    opacity: 0;
    transition: 1s;
  }

  &.scr{
    h2{
      color: #ffffffff;
      transform: translate3d(0px,20px,0px);
    }
    .left-block{
      opacity: 1;
      transform: translate3d(20px,0px,0px);
    }
    .right-block{
      opacity: 1;
      transform: translate3d(-20px,0px,0px);
    }
    #submit{
      opacity: 1;
    }

  }

  .form-block{
    font-weight: 500;
    background: #bd508f;
    padding: 35px;
    margin-bottom: 30px;
    input, label, textarea{
      width: 100%;

    }
    input, textarea{
      border: 1px solid #bd508f;
      padding: 10px;
      color: #000;
    }
    textarea{
      height: 290px;
    }
    label{
      text-align: left;
      margin-bottom: 25px;
      text-transform: uppercase;
    }
    #file, #email, #name{
      margin-bottom: 25px;
    }
    #file{
      width: 100%;
    }
  }
  button{
    background:  #ad2473;
    color: #fff;
    border: 1px solid #d796bc;
    padding: 10px;
  }
  #submit{
    padding: 10px 30px;
    opacity: 0;
    transition: 0.8s;

  }
}

#level5{
  h2{
    color: #44444400;
    transition: 0.6s;
  }
  p{
    font-size: 2em;
    margin: 50px 0 20px 0;
    opacity: 0;
    transition: 0.8s;
  }
  .contacts{
    text-align: left;
    margin: 20px 0;
    opacity: 0;
    transition: 0.8s;
  }
  &.scr{
    h2{
      color: #444444ff;
      transform: translate3d(0px,20px,0px);
    }
    p, .contacts{
      opacity: 1;
    }
  }
}
#map{
  height: 700px;
  margin: 0;
  padding: 0;
}
footer{
  background: #090909;
  text-align: center;
  padding: 100px 0 60px 0;
  text-transform: uppercase;
  .social{
    margin-bottom: 20px;
  }
  .btn-pay{
    background:  transparent;
    color: #fff;
    border: 1px solid #ad2473;
    padding: 10px;
    margin-bottom: 20px;
  }
}

.thumbnail{
  background: #fff;
  border: none;
  padding: 0;
}
